import './styles/Profession.css';
import axios from 'axios';
import React, {useState} from 'react';
import Ratio from "./components/Ratio";


let prof = {
    name: '',
    Verbal: 0,
    Computing: 0,
    Attentiveness: 0,
    CreativeThinking: 0,
    Analytic: 0,
    Logics: 0,
    WorkMemory: 0,
    Spatial: 0,
    Technical: 0
};

function Profession() {
    const [ratio] = useState(prof);
    const handleSubmit = (ev) => {
        ev.preventDefault();

        let myForm = ev.target;
        let fd = new FormData(myForm);

        let obj = {};
        for (let key of fd.keys()) {
            obj[key] = fd.get(key);
        }
        
        let Responser = {
            name: obj.name,
            Verbal: Number(obj.Verbal),
            Computing: Number(obj.Computing),
            Attentiveness: Number(obj.Attentiveness),
            CreativeThinking: Number(obj.CreativeThinking),
            Analytic: Number(obj.Analytic),
            Logics: Number(obj.Logics),
            WorkMemory: Number(obj.WorkMemory),
            Spatial: Number(obj.Spatial),
            Technical: Number(obj.Technical),
        };

        for (let propName in Responser) {
            if (Responser[propName] === 0) {
                delete Responser[propName];
            }
        }


        axios({
            method: 'post',
            url: 'http://localhost:5000/profession',
            data: Responser,
        });
        console.log(Responser);
    };
    return (
        <div className="Profession">
            <form method={'post'} onSubmit={handleSubmit}>
                <label>
                    Наименование профессии <br/>
                    <input name={'name'} type={'text'} placeholder={'Профессия'} defaultValue={ratio.name}/>
                </label>
                <Ratio post={ratio.Verbal} name={'Verbal'}/>
                <Ratio post={ratio.Computing} name={'Computing'}/>
                <Ratio post={ratio.Attentiveness} name={'Attentiveness'}/>
                <Ratio post={ratio.CreativeThinking} name={'CreativeThinking'}/>
                <Ratio post={ratio.Analytic} name={'Analytic'}/>
                <Ratio post={ratio.Logics} name={'Logics'}/>
                <Ratio post={ratio.WorkMemory} name={'WorkMemory'}/>
                <Ratio post={ratio.Spatial} name={'Spatial'}/>
                <Ratio post={ratio.Technical} name={'Technical'}/>
                <input type={"submit"} value={"Отправить"}/>
            </form>
            <br/>
            <form>
                <label>Список профессий</label>
            </form>
        </div>
    );
}

export default Profession;
