import React, {useState} from 'react';

const Ratio = (props) => {
    const [num, setNum] = useState(props.post);

    return (
        <div>
            <label>
                <br/>
                {props.name}
                <input
                    type="range"
                    min="0"
                    max="2"
                    name={props.name}
                    onChange = {event => setNum(event.target.value)}
                    defaultValue={num}

                />

            </label>
            <label> = {num}</label>
        </div>
    );
};

export default Ratio;